import React from 'react';
import styled from 'styled-components';

import { PostNode } from '@t/components';
import WeeklyPickListItem from '@/components/weekly/WeeklyPickListItem';
import { List } from '@/components/common/List';

interface WeeklyPickListProps {
  posts: {
    edges: PostNode[];
  };
  lang: 'ko' | 'en';
}

const Wrapper = styled.div`
  max-width: 1060px;
  margin: 0 auto;

  @media (max-width: 1200px) {
    padding: 0 12.5%;
  }

  @media (max-width: 720px) {
    padding: 0;
  }
`;

const WeeklyList = styled(List)`
  padding: 20px 0 60px;

  @media (max-width: 720px) {
    padding: 0;
  }
`;

const WeeklyPickList: React.FC<WeeklyPickListProps> = ({ posts, lang }) => (
  <Wrapper>
    <WeeklyList>
      {posts.edges.map((post) => (
        <WeeklyPickListItem key={post.node.frontmatter.id} post={post} lang={lang} />
      ))}
    </WeeklyList>
  </Wrapper>
);

export default WeeklyPickList;
