import React from 'react';
import styled from 'styled-components';

import LazyImage from '@/components/detail/LazyImage';
import { InlineBlockLink } from '@/components/common/Link';
import createGA from '@/components/common/createGA';

interface WeeklyPickThumbnailProps {
  thumbnail: string;
  url: string;
}

const Wrapper = styled.div`
  position: absolute;
  max-width: 100%;
  max-height: 100%;
  text-align: center;
`;

const WeeklyPickThumbnail: React.FC<WeeklyPickThumbnailProps> = ({ thumbnail, url }) => {
  const ga = createGA('Weekly Picks - main', 'click', 'thumbnail');

  return (
    <InlineBlockLink type="internal" url={url} ga={ga}>
      <Wrapper>
        <LazyImage src={thumbnail} />
      </Wrapper>
    </InlineBlockLink>
  );
};

export default WeeklyPickThumbnail;
